/**
 * CSS imports
 ============================================================================ */
import "./PressRelease.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Container, Row, Col, Form } from "react-bootstrap";

/**
   * User imports
   ============================================================================ */
import Card from "../card";
import Editor from "../editor";
import TextViewer from "../textViewer";
import Breadcrumb from "../breadcrumb";
import Search from "../search";

/**
   * Default export
   ============================================================================ */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = { namespace: "PressRelease", searchable: true };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const {
    siteEditMode,
    textCreated,
    textTitle,
    textAppetizer,
    textDescription,
    textID,
    helpPath2,
  } = props.barrelGetters;
  const {
    setTextTitle,
    setTextAppetizer,
    setTextDescription,
  } = props.barrelSetters;
  const { formatDate } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathNewsCategory } = props.barrelProps;

  /**
     * Module vars
     ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
     * Functions
     ============================================================================ */

  const onSave = ({ title, appetizer, text }) => {
    setTextTitle(title);
    setTextAppetizer(appetizer);
    setTextDescription(text);
  };

  const urlEncode = (rawStr) => {
    // return rawStr.replace(/[\u00A0-\u9999<>\&]/gim, function (i) {
    //   return "&#" + i.charCodeAt(0) + ";";
    // });

    return rawStr.replace(/\+/g, "%2B");
  };

  const searchOnSubmit = (search) => {
    const searchUrlEncoded = urlEncode(search);
    window.location = `/${pathNewsCategory}?search=${searchUrlEncoded}`;
  };

  const created = formatDate(textCreated, {
    month: "short",
    day: "numeric",
    year: "numeric",
  });

  /**
     * Return
     ============================================================================ */
  return (
    <>
      <Card title={false} header={false} border={0} maxWidthBody="100%">
        <Container fluid>
          {/**
           * Search
           */}
          {props.searchable && (
            <Row>
              <Col>
                <div className="press-release-title">
                  <h1>{textTitle}</h1>
                </div>
                <div className="press-release-created">{created}</div>
              </Col>
            </Row>
          )}
          <Row>
            <Col style={{}}>
              {/**
               * Viewer / Editor
               */}
              {!siteEditMode ? (
                <TextViewer {...args} />
              ) : (
                <Editor
                  {...args}
                  request="save text"
                  mode="html"
                  title={textTitle}
                  appetizer={textAppetizer}
                  showAppetizer={true}
                  text={textDescription}
                  onSave={onSave}
                  data={{
                    id: textID,
                    type: helpPath2,
                    status: 0,
                  }}
                />
              )}
            </Col>
          </Row>
        </Container>
      </Card>
    </>
  );
};
